header .top-main-linkbar{
  background-color: var(--green-color);
  min-height: 30px;
}


.aside-share-links{
  position: absolute;
  right: 0;
  top: 0;
}

body.page-home .aside-share-links{
  top: 16px;
}


.aside-share-links a {
  display: inline-block;
  opacity: 0.7;
  margin-right: 15px;
}

.aside-share-links a:last-of-type {
  margin-right: 0;
}

.aside-share-links a:hover {
  opacity: 1;
}

.aside-share-links a.share-f img {
  width: 13px;
  height: 28px;
}
.aside-share-links a.share-t img {
  width: 29px;
  height: 24px;
}



/* override default style to fix legend placement */
ul.slider li.slide p.legend{
  margin-left: -45% !important;
  display: none;
}

ul.slider li.slide.selected p.legend{
  display:inherit;
}

.embed-container { position: relative; padding-bottom: 56.25%; height: 0; overflow: hidden; max-width: 100%; }
.embed-container iframe, .embed-container object, .embed-container embed { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }
.bios {
  list-style-type: none;
  padding-left: 0;
  display: grid;
  grid-gap: 20px;
  max-width: 1100px;
  grid-template-areas: "p0"
                       "p1"
                       "p2"
                       "p3"
                       "p4";
}

@media(min-width:991px) {
  .bios {
    grid-template-areas: "p0 p1"
                         "p2 p3"
                         "p4 .";
  }
}

.bios .bio-item.person-0{
  grid-area: p0;
}
.bios .bio-item.person-1{
  grid-area: p1;
}
.bios .bio-item.person-2{
  grid-area: p2;
}
.bios .bio-item.person-3{
  grid-area: p3;
}
.bios .bio-item.person-4{
  grid-area: p4;
}


.aside-articles {
  margin-top: 48px;
}
.aside-articles .articles {
  list-style-type: none;
  padding-left: 0;
  display: grid;
  grid-gap: 20px;
  grid-template-areas: "a0"
                       "a1"
                       "a2"
                       "a3";
}

@media(min-width:650px) {
  .aside-articles .articles {
    grid-template-areas: "a0 a1"
                         "a2 a3";
  }
}

.aside-articles .articles .article.article-0{
  grid-area: a0;
}
.aside-articles .articles .article.article-1{
  grid-area: a1;
}
.aside-articles .articles .article.article-2{
  grid-area: a2;
}
.aside-articles .articles .article.article-3{
  grid-area: a3;
}

.home-map{
  text-align: center;
}
:root {
  --green-color: #00843D;
}

.main-content{
  position: relative;
}




